import { Helmet } from "react-helmet";
import React, { useState } from "react";
import "./ContactUs.css";

const ContactUs = () => {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    message: "",
  });

  const [submitted, setSubmitted] = useState(false);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    if (!formData.name.trim() || !formData.email.trim() || !formData.message.trim()) {
      alert("Please fill in all fields.");
      return;
    }

    // Here you can send formData to your backend or API
    console.log("Form Submitted:", formData);

    setSubmitted(true);
    setFormData({ name: "", email: "", message: "" });

    setTimeout(() => setSubmitted(false), 3000); // hide success message after 3s
  };

  return (
    <>
    <Helmet>
        <title>Contact Us - EmiBot AI Companion</title>
        <meta name="description" content="Get in touch with the EmiBot team for support or inquiries." />
        <meta name="keywords" content="contact, EmiBot, support" />
      </Helmet>
    <div className="contact-container">
      <h1 className="contact-title">Contact Us</h1>
      <p className="contact-subtitle">
        We'd love to hear from you! Send us your questions, feedback, or suggestions for EmiBot.
      </p>

      <form className="contact-form" onSubmit={handleSubmit}>
        <input
          type="text"
          name="name"
          placeholder="Your Name"
          value={formData.name}
          onChange={handleChange}
          className="contact-input"
        />

        <input
          type="email"
          name="email"
          placeholder="Your Email"
          value={formData.email}
          onChange={handleChange}
          className="contact-input"
        />

        <textarea
          name="message"
          placeholder="Write your message..."
          value={formData.message}
          onChange={handleChange}
          className="contact-textarea"
        />

        <button type="submit" className="contact-btn">
          Send Message
        </button>
      </form>

      {submitted && <p className="success-msg">Thank you! Your message has been sent.</p>}
    </div>
    </>
  );
};

export default ContactUs;
